import { Component } from '@angular/core';
import { style } from '@angular/core/src/animation/dsl';
import { ElementRef } from '@angular/core';

@Component({
    selector: 'my-app',
    templateUrl: "app.component.html",
    // styleUrls: ['/app.component.css']
    
})
export class AppComponent { 
  title = 'app';
    
    constructor(private myElement: ElementRef) {}

    gotoTop() {
        let el = this.myElement.nativeElement.querySelector('nav');
        el.scrollIntoView();
      }
      gotoHome(){
        let el = this.myElement.nativeElement.querySelector('app-home');
        el.scrollIntoView();
      }
      gotoAbout(){
        let el = this.myElement.nativeElement.querySelector('app-about');
        el.scrollIntoView();
      }
      gotoResume(){
        let el = this.myElement.nativeElement.querySelector('app-resume');
        el.scrollIntoView();
      }
      
      gotoFooter() {
        let el = this.myElement.nativeElement.querySelector('app-footer');
        el.scrollIntoView();
      }
}



